import React, { Suspense } from 'react';
import { contactsRoutes } from '../routes'
import { Switch, Redirect } from 'react-router-dom';
import { ThemeProvider } from 'styled-components'
import { useSelector } from "react-redux";
import Layout from './header/Layout';
import PublicRoute from './routes/PublicRoute';
import PrivateRoute from './routes/PrivetRoute';
import GlobalStyle from '../theme/GlobalStyle';
import light from '../theme/light';
import dark from '../theme/dark';
import usePersisterThemeHook from '../hooks/persisterTheme';




const App = () => {
    const isAuth = useSelector((state) => state.auth.isAuth);

    // const [theme, setTheme] = usePersisterThemeHook("theme", light);

    // const toggleTheme = () => {
    //     setTheme(theme.title === "light" ? dark : light);
    // }

    return (
        <>

            <Layout >
                <Suspense fallback=''>
                    <Switch>
                        {contactsRoutes.map(route => (
                            !route.isPrivate ?
                                < PublicRoute key={route.path} isAuth={isAuth} {...route} />
                                : <PrivateRoute key={route.path} isAuth={isAuth} {...route} />


                        ))}
                        <Redirect to="/" />
                    </Switch>
                </Suspense>
            </Layout>

        </>
    )
}


export default App

