import React from 'react'
import { Route, Redirect } from 'react-router-dom';

const PrivateRoute = ({ path, exact, component, isAuth }) => {
    return isAuth ? (
        <div>
            <Route path={path} exact={exact} component={component} />
        </div>
    ) : (
            <Redirect to='/login' />
        );
}

export default PrivateRoute